.section {
  height: 182em;
  display: flex;
  justify-content: space-between;
  padding: 48em 0 24em 0;
  box-sizing: border-box;
  border-bottom: 1px solid #d3d6d5;
  @media (orientation: portrait) {
    height: max-content;
    flex-direction: column;
  }
  .info {
    width: 70%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    gap: 8em;
    @media (orientation: portrait) {
      width: 100%;
    }

    .title {
      text-decoration: none;
      color: #282a2e;
      font-family: "Roboto Flex";
      font-style: normal;
      font-weight: 500;
      font-size: 24em;
      line-height: 140%;
    }
    .subtitle {
      font-family: "Roboto Flex";
      font-style: normal;
      font-weight: 400;
      font-size: 20em;
      line-height: 140%;
    }
    .links {
      display: flex;
      gap: 16em;
      @media (orientation: portrait) {
        flex-direction: column;
      }
      .link {
        text-decoration: none;
        display: flex;
        align-items: center;
        gap: 8px;
        font-family: "Roboto Flex";
        font-style: normal;
        font-weight: 400;
        font-size: 20em;
        line-height: 140%;
        color: #282a2e;
      }
    }

    .description {
      // margin-bottom: 24px;
      height: 3em;
      overflow: hidden;
      font-family: "Roboto Flex";
      font-style: normal;
      font-weight: 400;
      font-size: 20em;
      line-height: 140%;
      color: #888888;
    }
    .darkGreen * {
      fill: #245353;
    }
  }
  .buttons {
    display: flex;
    justify-content: flex-end;
    align-items: center;
  }
}
@media (orientation: portrait) {
  .arrow {
    display: none;
  }
}
